<template>
  <div id="app-help-container-react">
    <div class="zent-portal">
      <div id="app-help-container" style="min-height: 716px;">
        <div class="help-container-head">
          <i class="app-help-icon app-help-icon-help"></i>
          <span>帮助和服务</span>
          <i class="app-help-icon app-help-icon-close" @click.stop="handleHideHelp"></i>
        </div>
        <div class="help-container-body">
          <div class="help-body-title">帮助中心<a href="//help.youzan.com" target="_blank" rel="noopener noreferrer" class="pull-right">进入<i class="app-help-icon app-help-icon-more"></i></a></div>
          <div class="help-body-content">
            <p>
              <span style="color: #000000;">如何推广自己的店铺？</span>
              <br>
              <span style="color: #808080;">微信推广、微博推广、线下推广等方式引导买家进入店铺 。</span>
              <a href="https://help.youzan.com/qa#/menu/2212/detail/852?_k=ddusyg" target="_blank">详情<br></a>
            </p>
            <hr>
            <p><a href="https://help.youzan.com/qa#/menu/2211/detail/853?_k=bhw37j" target="_blank">如何将店铺或商品分享到朋友圈？</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  'name': 'help'
};

</script>
<style type="text/css">
#app-help-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 200px;
  min-height: 1400px;
  background: #fff;
  z-index: 99;
  border-left: 1px solid #e5e5e5;
}

#app-help-container .help-container-head {
  line-height: 50px;
  height: 50px;
  padding: 0 20px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border-bottom: 1px solid #e5e5e5
}

#app-help-container .help-container-body {
  padding: 10px 10px 60px
}

#app-help-container .help-body-content {
  padding: 10px;
  margin-bottom: -20px
}

#app-help-container .help-body-content p {
  margin-bottom: .5em;
  line-height: 1.8em
}

#app-help-container .help-body-content span {
  white-space: normal !important
}

#app-help-container .help-body-content hr {
  border: none;
  border-top: 1px solid #e5e5e5;
  margin: 20px 0
}

#app-help-container .help-body-title {
  color: #333;
  border-left: 2px solid #3283fa;
  padding-left: 8px;
  padding-right: 10px;
  line-height: 12px;
  margin: 10px 0
}

#app-help-container .help-body-split-line {
  border-top: 1px solid #e5e5e5;
  margin: 20px 10px
}

#app-help-container .help-body-service {
  position: relative
}

#app-help-container .help-body-service .service-region {
  padding: 20px 20px 20px 70px;
  background-color: #f8f8f8;
  border-radius: 2px;
  display: block;
  color: #666
}

#app-help-container .help-body-service .service-region:hover {
  background: #f2f2f2
}

#app-help-container .help-body-service .service-avatar {
  position: absolute;
  top: 16px;
  left: 14px;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%
}

#app-help-container .help-body-service .service-avatar .red-point {
  display: inline-block;
  width: 15px;
  height: 15px;
  position: absolute;
  border: 1px solid white;
  background-color: #f44;
  border-radius: 50%;
  top: -4px;
  right: -4px
}

#app-help-container .help-body-service .service-name {
  color: #333;
  font-size: 16px;
  line-height: 1em;
  margin-bottom: 5px
}

#app-help-container .help-body-service .service-name-title {
  color: #999
}

#app-help-container .help-body-service .customer-contact-block {
  display: block;
  border-top: 2px solid #fff;
  background-color: #f8f8f8;
  border-radius: 2px
}

#app-help-container .help-body-service .customer-contact-block:hover {
  background: #f2f2f2
}

#app-help-container .help-body-service .panama-customer-block {
  position: relative;
  display: block;
  padding: 8px 0
}

#app-help-container .help-body-service .panama-customer-block .panama-customer-button {
  display: block;
  margin: 0 15px;
  border: 1px solid #bbb;
  background: #fff;
  border-radius: 2px;
  text-align: center;
  line-height: 24px;
  color: #333
}

#app-help-container .help-body-service .panama-customer-block .panama-customer-button .panama-customer-inner {
  position: relative;
  display: block;
  padding-left: 20px;
  width: 55px;
  margin: 0 auto;
  background: url(data:image/png;base64,) no-repeat left center;
  background-size: 15px
}

#app-help-container .help-body-service .panama-customer-block .panama-customer-button .panama-customer-inner .unread {
  display: inline-block;
  width: 6px;
  height: 6px;
  position: absolute;
  color: #fff;
  border: 1px solid white;
  background-color: #f44;
  border-radius: 50%;
  top: 3px;
  right: -6px;
  overflow: hidden
}

#app-help-container .help-body-service .service-hotline {
  padding: 14px 0;
  margin-bottom: 30px;
  font-size: 12px;
  text-align: center;
  border-top: 2px solid #fff;
  color: #333
}

#app-help-container.show-help {
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
}

</style>



